import styled from "styled-components";
import {Link} from 'react-router-dom';

const Footer = styled.footer`
min-width: 1200px;
font-family: 'GmarketSansMedium';

.sr-only{position:absolute;left:-9999px;}
>.working{
    border-top: 1px solid #aaa;

    >p{
        padding-left: 7%;
        height: 80px;
        line-height: 80px;

        >a{
            margin: 0 30px;
            &:hover{text-decoration: underline;}
        }
    }//end of p

}//end of .working

>.info{
    background-color: #fff;

    >.container{
        position: relative;
        padding: 45px 7% 40px;

        >h2,
        >p{
            margin-bottom: 15px;
            font-size: 13px;

            >a{}

            >.bar{margin: 0 10px;}
        }//end of p

        >h2:after{
            display: block;
            position: absolute;
            right: 7%;
            top: 50%;
            width: 53px;
            height: 68px;
            margin-top: -34px;
            content: '';

        }//end of h2:after
    }//end of .container
}//end of .info
`;

const Foot = ()=>{
    return (
        <Footer>
            <article class="info">
                <div class="container">
                    <p class="iam_1">Javascript, jQuery, React, css, html5</p>
                    <p class="iam_2">프론트앤드 개발자<span> 유지은</span></p>
                    <h2>&copy; 2022 Designed by 유지은</h2>
                </div>
            </article>   
        </Footer>
    );
}

export default Foot;